import React, { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import {
  Box,
  Typography,
  Button,
  Paper,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
  IconButton,
  Chip,
  Dialog,
  DialogActions,
  DialogContent,
  DialogContentText,
  DialogTitle,
  Tooltip,
  CircularProgress,
  Alert,
} from '@mui/material';
import {
  Add as AddIcon,
  Edit as EditIcon,
  Delete as DeleteIcon,
  Refresh as RefreshIcon,
  Check as CheckIcon,
  PlayArrow as PlayArrowIcon,
} from '@mui/icons-material';
import { toast } from 'react-toastify';

import apiKeyService from '../services/apiKeyService';
import { formatRelativeTime } from '../utils/dateUtils';

const ApiKeysPage = () => {
  const navigate = useNavigate();
  
  // 
  const [apiKeys, setApiKeys] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);
  const [deleteDialogOpen, setDeleteDialogOpen] = useState(false);
  const [deleteApiKeyId, setDeleteApiKeyId] = useState(null);
  const [testingApiKeyId, setTestingApiKeyId] = useState(null);
  
  // API
  const loadApiKeys = async () => {
    try {
      setLoading(true);
      setError(null);
      
      const data = await apiKeyService.getAllApiKeys();
      setApiKeys(data);
    } catch (error) {
      console.error('API:', error);
      setError(error.message);
    } finally {
      setLoading(false);
    }
  };
  
  // 
  useEffect(() => {
    loadApiKeys();
  }, []);
  
  // API
  const handleTestApiKey = async (id) => {
    try {
      setTestingApiKeyId(id);
      
      const success = await apiKeyService.testApiKey(id);
      
      if (success) {
        toast.success('API');
      }
    } catch (error) {
      console.error('API:', error);
      toast.error(error.message || 'API');
    } finally {
      setTestingApiKeyId(null);
    }
  };
  
  // API
  const handleOpenDeleteDialog = (id) => {
    setDeleteApiKeyId(id);
    setDeleteDialogOpen(true);
  };
  
  // API
  const handleDeleteApiKey = async () => {
    if (!deleteApiKeyId) return;
    
    try {
      const success = await apiKeyService.deleteApiKey(deleteApiKeyId);
      
      if (success) {
        toast.success('API');
        // 
        loadApiKeys();
      }
    } catch (error) {
      console.error('API:', error);
      toast.error(error.message || 'API');
    } finally {
      // 
      setDeleteDialogOpen(false);
      setDeleteApiKeyId(null);
    }
  };
  
  // 
  if (loading && apiKeys.length === 0) {
    return (
      <Box sx={{ display: 'flex', justifyContent: 'center', alignItems: 'center', height: '70vh' }}>
        <CircularProgress />
      </Box>
    );
  }

  return (
    <Box sx={{ pb: 4 }}>
      {/*  */}
      {error && (
        <Alert severity="error" sx={{ mb: 3 }}>
          {error}
        </Alert>
      )}
      
      {/*  */}
      <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', mb: 3 }}>
        <Typography variant="h4" component="h1">
          API
        </Typography>
        <Box>
          <Button
            variant="contained"
            startIcon={<AddIcon />}
            onClick={() => navigate('/api-keys/new')}
            sx={{ mr: 1 }}
          >
            API
          </Button>
          <IconButton
            onClick={loadApiKeys}
            disabled={loading}
            color="primary"
          >
            {loading ? <CircularProgress size={24} /> : <RefreshIcon />}
          </IconButton>
        </Box>
      </Box>
      
      {/* API */}
      <TableContainer component={Paper}>
        <Table>
          <TableHead>
            <TableRow>
              <TableCell></TableCell>
              <TableCell></TableCell>
              <TableCell></TableCell>
              <TableCell></TableCell>
              <TableCell></TableCell>
              <TableCell align="right"></TableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {apiKeys.length > 0 ? (
              apiKeys.map((apiKey) => (
                <TableRow key={apiKey.id}>
                  <TableCell>{apiKey.name}</TableCell>
                  <TableCell>{apiKey.accessKey}</TableCell>
                  <TableCell>{apiKey.btcWalletAddress}</TableCell>
                  <TableCell>
                    <Chip
                      label={apiKey.isActive ? '' : ''}
                      color={apiKey.isActive ? 'success' : 'default'}
                      size="small"
                    />
                  </TableCell>
                  <TableCell>
                    {apiKey.lastCheckedAt
                      ? formatRelativeTime(apiKey.lastCheckedAt)
                      : ''}
                  </TableCell>
                  <TableCell align="right">
                    <Tooltip title="">
                      <IconButton
                        color="primary"
                        onClick={() => handleTestApiKey(apiKey.id)}
                        disabled={testingApiKeyId === apiKey.id}
                      >
                        {testingApiKeyId === apiKey.id ? (
                          <CircularProgress size={20} />
                        ) : (
                          <PlayArrowIcon />
                        )}
                      </IconButton>
                    </Tooltip>
                    <Tooltip title="">
                      <IconButton
                        color="primary"
                        onClick={() => navigate(`/api-keys/${apiKey.id}`)}
                      >
                        <EditIcon />
                      </IconButton>
                    </Tooltip>
                    <Tooltip title="">
                      <IconButton
                        color="error"
                        onClick={() => handleOpenDeleteDialog(apiKey.id)}
                      >
                        <DeleteIcon />
                      </IconButton>
                    </Tooltip>
                  </TableCell>
                </TableRow>
              ))
            ) : (
              <TableRow>
                <TableCell colSpan={6} align="center">
                  <Typography variant="body1" sx={{ py: 3 }}>
                    API
                  </Typography>
                  <Button
                    variant="outlined"
                    startIcon={<AddIcon />}
                    onClick={() => navigate('/api-keys/new')}
                  >
                    API
                  </Button>
                </TableCell>
              </TableRow>
            )}
          </TableBody>
        </Table>
      </TableContainer>
      
      {/* API */}
      <Dialog
        open={deleteDialogOpen}
        onClose={() => setDeleteDialogOpen(false)}
      >
        <DialogTitle>API</DialogTitle>
        <DialogContent>
          <DialogContentText>
            API<br />
            
          </DialogContentText>
        </DialogContent>
        <DialogActions>
          <Button
            onClick={() => setDeleteDialogOpen(false)}
            color="primary"
          >
            
          </Button>
          <Button
            onClick={handleDeleteApiKey}
            color="error"
            variant="contained"
          >
            
          </Button>
        </DialogActions>
      </Dialog>
    </Box>
  );
};

export default ApiKeysPage;