import React, { useState, useEffect } from 'react';
import {
  Box,
  Typography,
  Card,
  CardContent,
  Grid,
  Divider,
  Button,
  FormControl,
  InputLabel,
  Select,
  MenuItem,
  FormControlLabel,
  Switch,
  TextField,
  Alert,
  Snackbar,
  CircularProgress
} from '@mui/material';
import api from '../../services/api'; // 

// 
const SyncSettingsContent = () => {
  // 
  const [settings, setSettings] = useState({
    isEnabled: true,
    balanceSyncInterval: 15, // 
    depositCheckInterval: 5, // 
    startTime: '00:00', // 
    endTime: '23:59', // 
    retryCount: 3,
    notifyOnError: true
  });

  // UI
  const [loading, setLoading] = useState(true);
  const [saving, setSaving] = useState(false);
  const [snackbar, setSnackbar] = useState({
    open: false,
    message: '',
    severity: 'success'
  });

  // 
  useEffect(() => {
    const fetchSettings = async () => {
      try {
        setLoading(true);
        // API
        const response = await api.get('/api/settings/sync');
        
        if (response.data && response.data.status === 'success') {
          setSettings({
            ...response.data.data,
            startTime: response.data.data.startTime || '00:00',
            endTime: response.data.data.endTime || '23:59'
          });
        }
      } catch (error) {
        console.error(':', error);
        setSnackbar({
          open: true,
          message: '',
          severity: 'error'
        });
      } finally {
        setLoading(false);
      }
    };

    fetchSettings();
  }, []);

  // 
  const handleChange = (event) => {
    const { name, value, checked } = event.target;
    
    if (name === 'isEnabled' || name === 'notifyOnError') {
      setSettings(prev => ({
        ...prev,
        [name]: checked
      }));
    } else {
      setSettings(prev => ({
        ...prev,
        [name]: value
      }));
    }
  };

  // 
  const handleSave = async () => {
    try {
      setSaving(true);
      
      // API
      const response = await api.post('/api/settings/sync', settings);
      
      if (response.data && response.data.status === 'success') {
        setSnackbar({
          open: true,
          message: '',
          severity: 'success'
        });
      }
    } catch (error) {
      console.error(':', error);
      setSnackbar({
        open: true,
        message: '',
        severity: 'error'
      });
    } finally {
      setSaving(false);
    }
  };

  // 
  const handleCloseSnackbar = () => {
    setSnackbar(prev => ({
      ...prev,
      open: false
    }));
  };

  if (loading) {
    return (
      <Card>
        <CardContent>
          <Box sx={{ display: 'flex', justifyContent: 'center', p: 3 }}>
            <CircularProgress />
          </Box>
        </CardContent>
      </Card>
    );
  }

  return (
    <Card>
      <CardContent>
        <Typography variant="h5" component="h2" gutterBottom>
          
        </Typography>
        <Divider sx={{ mb: 3 }} />
        
        <Grid container spacing={3}>
          {/* / */}
          <Grid item xs={12}>
            <FormControlLabel
              control={
                <Switch
                  checked={settings.isEnabled}
                  onChange={handleChange}
                  name="isEnabled"
                  color="primary"
                />
              }
              label=""
            />
            <Typography variant="body2" color="text.secondary">
              ONCoincheck
            </Typography>
          </Grid>
          
          {/*  */}
          <Grid item xs={12} md={6}>
            <FormControl fullWidth>
              <InputLabel id="balance-sync-interval-label"></InputLabel>
              <Select
                labelId="balance-sync-interval-label"
                id="balanceSyncInterval"
                name="balanceSyncInterval"
                value={settings.balanceSyncInterval}
                label=""
                onChange={handleChange}
                disabled={!settings.isEnabled}
              >
                <MenuItem value={5}>5</MenuItem>
                <MenuItem value={10}>10</MenuItem>
                <MenuItem value={15}>15</MenuItem>
                <MenuItem value={30}>30</MenuItem>
                <MenuItem value={60}>1</MenuItem>
              </Select>
            </FormControl>
            <Typography variant="body2" color="text.secondary" sx={{ mt: 1 }}>
              Coincheck
            </Typography>
          </Grid>
          
          {/*  */}
          <Grid item xs={12} md={6}>
            <FormControl fullWidth>
              <InputLabel id="deposit-check-interval-label"></InputLabel>
              <Select
                labelId="deposit-check-interval-label"
                id="depositCheckInterval"
                name="depositCheckInterval"
                value={settings.depositCheckInterval}
                label=""
                onChange={handleChange}
                disabled={!settings.isEnabled}
              >
                <MenuItem value={1}>1</MenuItem>
                <MenuItem value={3}>3</MenuItem>
                <MenuItem value={5}>5</MenuItem>
                <MenuItem value={10}>10</MenuItem>
                <MenuItem value={15}>15</MenuItem>
              </Select>
            </FormControl>
            <Typography variant="body2" color="text.secondary" sx={{ mt: 1 }}>
              
            </Typography>
          </Grid>
          
          {/*  */}
          <Grid item xs={12} md={6}>
            <TextField
              fullWidth
              id="startTime"
              name="startTime"
              label=" (HH:MM)"
              type="time"
              value={settings.startTime}
              onChange={handleChange}
              InputLabelProps={{
                shrink: true,
              }}
              inputProps={{
                step: 300, // 5
              }}
              disabled={!settings.isEnabled}
            />
            <Typography variant="body2" color="text.secondary" sx={{ mt: 1 }}>
              
            </Typography>
          </Grid>
          
          {/*  */}
          <Grid item xs={12} md={6}>
            <TextField
              fullWidth
              id="endTime"
              name="endTime"
              label=" (HH:MM)"
              type="time"
              value={settings.endTime}
              onChange={handleChange}
              InputLabelProps={{
                shrink: true,
              }}
              inputProps={{
                step: 300, // 5
              }}
              disabled={!settings.isEnabled}
            />
            <Typography variant="body2" color="text.secondary" sx={{ mt: 1 }}>
              
            </Typography>
          </Grid>
          
          {/*  */}
          <Grid item xs={12} md={6}>
            <TextField
              fullWidth
              id="retryCount"
              name="retryCount"
              label=""
              type="number"
              value={settings.retryCount}
              onChange={handleChange}
              disabled={!settings.isEnabled}
              InputProps={{ inputProps: { min: 0, max: 10 } }}
            />
            <Typography variant="body2" color="text.secondary" sx={{ mt: 1 }}>
              
            </Typography>
          </Grid>
          
          {/*  */}
          <Grid item xs={12} md={6}>
            <FormControlLabel
              control={
                <Switch
                  checked={settings.notifyOnError}
                  onChange={handleChange}
                  name="notifyOnError"
                  color="primary"
                  disabled={!settings.isEnabled}
                />
              }
              label=""
            />
            <Typography variant="body2" color="text.secondary">
              
            </Typography>
          </Grid>
          
          {/*  */}
          <Grid item xs={12}>
            <Alert severity="info" sx={{ mt: 2 }}>
              <Typography variant="body2">
                 : Coincheck API155
              </Typography>
            </Alert>
          </Grid>
          
          {/*  */}
          <Grid item xs={12}>
            <Box sx={{ display: 'flex', justifyContent: 'flex-end', mt: 2 }}>
              <Button
                variant="contained"
                color="primary"
                onClick={handleSave}
                disabled={saving}
                startIcon={saving && <CircularProgress size={20} />}
              >
                {saving ? '...' : ''}
              </Button>
            </Box>
          </Grid>
        </Grid>
      </CardContent>
      
      {/*  */}
      <Snackbar
        open={snackbar.open}
        autoHideDuration={6000}
        onClose={handleCloseSnackbar}
        anchorOrigin={{ vertical: 'bottom', horizontal: 'right' }}
      >
        <Alert onClose={handleCloseSnackbar} severity={snackbar.severity} sx={{ width: '100%' }}>
          {snackbar.message}
        </Alert>
      </Snackbar>
    </Card>
  );
};

export default SyncSettingsContent;