import React, { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import {
  Box,
  Typography,
  Button,
  Paper,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
  TablePagination,
  IconButton,
  Chip,
  Tooltip,
  CircularProgress,
  Alert,
  Dialog,
  DialogActions,
  DialogContent,
  DialogContentText,
  DialogTitle,
  TextField,
  MenuItem,
  Select,
  FormControl,
  InputLabel,
} from '@mui/material';
import {
  Refresh as RefreshIcon,
  PlayArrow as ExecuteIcon,
  Visibility as VisibilityIcon,
} from '@mui/icons-material';
import { toast } from 'react-toastify';

import transactionService from '../services/transactionService';
import apiKeyService from '../services/apiKeyService';
import { formatDateTime } from '../utils/dateUtils';

// 
const getTypeColor = (type) => {
  switch (type) {
    case 'deposit':
      return 'primary';
    case 'purchase':
      return 'info';
    case 'transfer':
      return 'success';
    case 'error':
      return 'error';
    default:
      return 'default';
  }
};

// 
const getStatusColor = (status) => {
  switch (status) {
    case 'completed':
      return 'success';
    case 'pending':
      return 'warning';
    case 'failed':
      return 'error';
    default:
      return 'default';
  }
};

// 
const getTypeName = (type) => {
  switch (type) {
    case 'deposit':
      return '';
    case 'purchase':
      return '';
    case 'transfer':
      return '';
    case 'error':
      return '';
    default:
      return type;
  }
};

const TransactionsPage = () => {
  const navigate = useNavigate();
  
  // 
  const [transactions, setTransactions] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);
  const [pagination, setPagination] = useState({
    total: 0,
    page: 0,
    limit: 10,
    pages: 0,
  });
  
  // 
  const [executeDialogOpen, setExecuteDialogOpen] = useState(false);
  const [apiKeys, setApiKeys] = useState([]);
  const [executingTransaction, setExecutingTransaction] = useState(false);
  const [transactionForm, setTransactionForm] = useState({
    apiKeyId: '',
    amount: '',
  });
  
  // 
  const loadTransactions = async (page = 0, limit = 10) => {
    try {
      setLoading(true);
      setError(null);
      
      const { transactions: data, pagination: paginationData } = await transactionService.getAllTransactions({
        page: page + 1, // API1MUI0
        limit,
      });
      
      setTransactions(data);
      setPagination({
        ...paginationData,
        page, // MUI0
      });
    } catch (error) {
      console.error(':', error);
      setError(error.message);
    } finally {
      setLoading(false);
    }
  };
  
  // 
  useEffect(() => {
    loadTransactions();
  }, []);
  
  // 
  const handleChangePage = (event, newPage) => {
    loadTransactions(newPage, pagination.limit);
  };
  
  // 1
  const handleChangeRowsPerPage = (event) => {
    const newLimit = parseInt(event.target.value, 10);
    loadTransactions(0, newLimit);
  };
  
  // 
  const handleOpenExecuteDialog = async () => {
    try {
      // API
      const apiKeyList = await apiKeyService.getAllApiKeys();
      setApiKeys(apiKeyList.filter(key => key.isActive));
      
      // 
      setTransactionForm({
        apiKeyId: apiKeyList.length > 0 ? apiKeyList[0].id : '',
        amount: '',
      });
      
      setExecuteDialogOpen(true);
    } catch (error) {
      console.error('API:', error);
      toast.error('API');
    }
  };
  
  // 
  const handleFormChange = (e) => {
    const { name, value } = e.target;
    setTransactionForm((prev) => ({ ...prev, [name]: value }));
  };
  
  // 
  const handleExecuteTransaction = async () => {
    try {
      setExecutingTransaction(true);
      
      // 
      if (!transactionForm.apiKeyId || !transactionForm.amount) {
        toast.error('API');
        return;
      }
      
      // 
      const result = await transactionService.executeManualTransaction(transactionForm);
      
      toast.success('');
      
      // 
      setExecuteDialogOpen(false);
      
      // 
      navigate(`/transactions/${result.id}`);
    } catch (error) {
      console.error(':', error);
      toast.error(error.message || '');
    } finally {
      setExecutingTransaction(false);
    }
  };
  
  // 
  if (loading && transactions.length === 0) {
    return (
      <Box sx={{ display: 'flex', justifyContent: 'center', alignItems: 'center', height: '70vh' }}>
        <CircularProgress />
      </Box>
    );
  }

  return (
    <Box sx={{ pb: 4 }}>
      {/*  */}
      {error && (
        <Alert severity="error" sx={{ mb: 3 }}>
          {error}
        </Alert>
      )}
      
      {/*  */}
      <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', mb: 3 }}>
        <Typography variant="h4" component="h1">
          
        </Typography>
        <Box>
          <Button
            variant="contained"
            startIcon={<ExecuteIcon />}
            onClick={handleOpenExecuteDialog}
            sx={{ mr: 1 }}
          >
            
          </Button>
          <IconButton
            onClick={() => loadTransactions(pagination.page, pagination.limit)}
            disabled={loading}
            color="primary"
          >
            {loading ? <CircularProgress size={24} /> : <RefreshIcon />}
          </IconButton>
        </Box>
      </Box>
      
      {/*  */}
      <TableContainer component={Paper} sx={{ mb: 3 }}>
        <Table>
          <TableHead>
            <TableRow>
              <TableCell>ID</TableCell>
              <TableCell></TableCell>
              <TableCell></TableCell>
              <TableCell>API</TableCell>
              <TableCell></TableCell>
              <TableCell></TableCell>
              <TableCell align="right"></TableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {transactions.length > 0 ? (
              transactions.map((transaction) => (
                <TableRow key={transaction.id}>
                  <TableCell>{transaction.id}</TableCell>
                  <TableCell>{formatDateTime(transaction.createdAt)}</TableCell>
                  <TableCell>
                    <Chip
                      label={getTypeName(transaction.type)}
                      color={getTypeColor(transaction.type)}
                      size="small"
                    />
                  </TableCell>
                  <TableCell>
                    {transaction.apiKey ? transaction.apiKey.name : '-'}
                  </TableCell>
                  <TableCell>
                    {transaction.type === 'deposit' && transaction.depositAmount ? 
                      `${transaction.depositAmount} BTC` : null}
                    {transaction.type === 'purchase' && transaction.purchaseAmount ? 
                      `${transaction.purchaseAmount} BTC` : null}
                    {transaction.type === 'transfer' && transaction.transferAmount ? 
                      `${transaction.transferAmount} BTC` : null}
                    {transaction.type === 'error' ? '-' : null}
                  </TableCell>
                  <TableCell>
                    <Chip
                      label={transaction.status}
                      color={getStatusColor(transaction.status)}
                      size="small"
                    />
                  </TableCell>
                  <TableCell align="right">
                    <Tooltip title="">
                      <IconButton
                        color="primary"
                        onClick={() => navigate(`/transactions/${transaction.id}`)}
                      >
                        <VisibilityIcon />
                      </IconButton>
                    </Tooltip>
                  </TableCell>
                </TableRow>
              ))
            ) : (
              <TableRow>
                <TableCell colSpan={7} align="center">
                  <Typography variant="body1" sx={{ py: 3 }}>
                    
                  </Typography>
                </TableCell>
              </TableRow>
            )}
          </TableBody>
        </Table>
      </TableContainer>
      
      {/*  */}
      {transactions.length > 0 && (
        <TablePagination
          component="div"
          count={pagination.total}
          page={pagination.page}
          onPageChange={handleChangePage}
          rowsPerPage={pagination.limit}
          onRowsPerPageChange={handleChangeRowsPerPage}
          rowsPerPageOptions={[5, 10, 25, 50]}
          labelRowsPerPage="1:"
          labelDisplayedRows={({ from, to, count }) => 
            `${from}-${to} / ${count}`}
        />
      )}
      
      {/*  */}
      <Dialog
        open={executeDialogOpen}
        onClose={() => !executingTransaction && setExecuteDialogOpen(false)}
      >
        <DialogTitle></DialogTitle>
        <DialogContent>
          <DialogContentText sx={{ mb: 3 }}>
            APIBTC<br />
            API
          </DialogContentText>
          
          <FormControl fullWidth sx={{ mb: 3 }}>
            <InputLabel id="api-key-select-label">API</InputLabel>
            <Select
              labelId="api-key-select-label"
              id="apiKeyId"
              name="apiKeyId"
              value={transactionForm.apiKeyId}
              onChange={handleFormChange}
              label="API"
              disabled={executingTransaction || apiKeys.length === 0}
            >
              {apiKeys.length > 0 ? (
                apiKeys.map((apiKey) => (
                  <MenuItem key={apiKey.id} value={apiKey.id}>
                    {apiKey.name}
                  </MenuItem>
                ))
              ) : (
                <MenuItem disabled>API</MenuItem>
              )}
            </Select>
          </FormControl>
          
          <TextField
            fullWidth
            id="amount"
            name="amount"
            label=" ()"
            type="number"
            value={transactionForm.amount}
            onChange={handleFormChange}
            disabled={executingTransaction}
            InputProps={{
              endAdornment: <Typography variant="body2">JPY</Typography>,
            }}
            helperText="BTC"
          />
        </DialogContent>
        <DialogActions>
          <Button
            onClick={() => setExecuteDialogOpen(false)}
            disabled={executingTransaction}
          >
            
          </Button>
          <Button
            onClick={handleExecuteTransaction}
            color="primary"
            variant="contained"
            disabled={executingTransaction || !transactionForm.apiKeyId || !transactionForm.amount}
          >
            {executingTransaction ? <CircularProgress size={24} /> : ''}
          </Button>
        </DialogActions>
      </Dialog>
    </Box>
  );
};

export default TransactionsPage;