import api from './api';

/**
 * APIAPI
 */
class ApiKeyService {
  /**
   * API
   * @returns {Promise<Array>} API
   */
  async getAllApiKeys() {
    try {
      const response = await api.get('/api/api-keys');
      
      if (response.data.status === 'success') {
        return response.data.data.apiKeys;
      }
      
      throw new Error(response.data.message || 'API');
    } catch (error) {
      this._handleError(error, 'API');
    }
  }

  /**
   * API
   * @param {number} id APIID
   * @param {boolean} edit 
   * @returns {Promise<Object>} API
   */
  async getApiKeyById(id, edit = false) {
    try {
      const url = edit 
        ? `/api/api-keys/${id}?mode=edit`
        : `/api/api-keys/${id}`;
        
      const response = await api.get(url);
      
      if (response.data.status === 'success') {
        return response.data.data.apiKey;
      }
      
      throw new Error(response.data.message || 'API');
    } catch (error) {
      this._handleError(error, 'API');
    }
  }

  /**
   * API
   * @param {Object} apiKeyData API
   * @returns {Promise<Object>} API
   */
  async createApiKey(apiKeyData) {
    try {
      const response = await api.post('/api/api-keys', apiKeyData);
      
      if (response.data.status === 'success') {
        return response.data.data.apiKey;
      }
      
      throw new Error(response.data.message || 'API');
    } catch (error) {
      this._handleError(error, 'API');
    }
  }

  /**
   * API
   * @param {number} id APIID
   * @param {Object} apiKeyData API
   * @returns {Promise<Object>} API
   */
  async updateApiKey(id, apiKeyData) {
    try {
      const response = await api.put(`/api/api-keys/${id}`, apiKeyData);
      
      if (response.data.status === 'success') {
        return response.data.data.apiKey;
      }
      
      throw new Error(response.data.message || 'API');
    } catch (error) {
      this._handleError(error, 'API');
    }
  }

  /**
   * API
   * @param {number} id APIID
   * @returns {Promise<boolean>} 
   */
  async deleteApiKey(id) {
    try {
      const response = await api.delete(`/api/api-keys/${id}`);
      
      return response.data.status === 'success';
    } catch (error) {
      this._handleError(error, 'API');
    }
  }

  /**
   * API
   * @param {number} id APIID
   * @returns {Promise<boolean>} 
   */
  async testApiKey(id) {
    try {
      const response = await api.post(`/api/api-keys/${id}/test`);
      
      return response.data.status === 'success';
    } catch (error) {
      this._handleError(error, 'API');
    }
  }

  /**
   * 
   * @private
   */
  _handleError(error, defaultMessage) {
    if (error.response) {
      // 
      if (error.response.status === 429) {
        throw new Error('API');
      }
      
      // 
      throw new Error(error.response.data.message || defaultMessage);
    }
    
    // 
    throw new Error('');
  }
}

export default new ApiKeyService();