import React, { useState } from 'react';
import {
  Box,
  Typography,
  Card,
  CardContent,
  Grid,
  Divider,
  Button,
  ListItemText,
  ListItemIcon,
  MenuItem,
  MenuList,
  Paper,
  Alert,
} from '@mui/material';
import {
  Person as PersonIcon,
  VpnKey as VpnKeyIcon,
  Sync as SyncIcon,
  Notifications as NotificationsIcon,
  Security as SecurityIcon,
  Info as InfoIcon,
} from '@mui/icons-material';
import { useNavigate } from 'react-router-dom';
import { useAuth } from '../contexts/AuthContext';
import { formatDateTime } from '../utils/dateUtils';
import SyncSettingsContent from '../components/settings/SyncSettingsContent';

const SettingsPage = () => {
  const { user } = useAuth();
  const navigate = useNavigate();
  
  // 
  const [selectedMenu, setSelectedMenu] = useState('profile');

  // 
  const handleMenuSelect = (menu) => {
    setSelectedMenu(menu);
  };

  // 
  const renderProfileContent = () => (
    <>
      <Card>
        <CardContent>
          <Typography variant="h5" component="h2" gutterBottom>
            
          </Typography>
          <Divider sx={{ mb: 3 }} />
          
          <Grid container spacing={3}>
            <Grid item xs={12} md={6}>
              <Typography variant="subtitle2" color="text.secondary">
                
              </Typography>
              <Typography variant="body1">
                {user?.username || '-'}
              </Typography>
            </Grid>
            
            <Grid item xs={12} md={6}>
              <Typography variant="subtitle2" color="text.secondary">
                
              </Typography>
              <Typography variant="body1">
                {user?.email || '-'}
              </Typography>
            </Grid>
            
            <Grid item xs={12} md={6}>
              <Typography variant="subtitle2" color="text.secondary">
                
              </Typography>
              <Typography variant="body1">
                {user?.role === 'admin' ? '' : ''}
              </Typography>
            </Grid>
            
            <Grid item xs={12} md={6}>
              <Typography variant="subtitle2" color="text.secondary">
                
              </Typography>
              <Typography variant="body1">
                {user?.lastLoginAt ? formatDateTime(user.lastLoginAt) : '-'}
              </Typography>
            </Grid>
            
            <Grid item xs={12} md={6}>
              <Typography variant="subtitle2" color="text.secondary">
                
              </Typography>
              <Typography variant="body1">
                {user?.createdAt ? formatDateTime(user.createdAt) : '-'}
              </Typography>
            </Grid>
            
            <Grid item xs={12}>
              <Divider sx={{ my: 2 }} />
              <Typography variant="h6" component="h3" gutterBottom>
                
              </Typography>
            </Grid>
            
            <Grid item xs={12}>
              <Button variant="outlined" color="primary">
                
              </Button>
              <Button variant="outlined" color="primary" sx={{ ml: 2 }}>
                
              </Button>
            </Grid>
          </Grid>
        </CardContent>
      </Card>
    </>
  );

  // 
  const renderSecurityContent = () => (
    <Card>
      <CardContent>
        <Typography variant="h5" component="h2" gutterBottom>
          
        </Typography>
        <Divider sx={{ mb: 3 }} />
        
        <Alert severity="info" sx={{ mb: 3 }}>
          
        </Alert>
        
        {/*  */}
        <Typography variant="body1">
          
        </Typography>
      </CardContent>
    </Card>
  );

  // API
  const renderApiKeyContent = () => (
    <Card>
      <CardContent>
        <Typography variant="h5" component="h2" gutterBottom>
          API
        </Typography>
        <Divider sx={{ mb: 3 }} />
        
        <Alert severity="info" sx={{ mb: 3 }}>
          APIAPI
        </Alert>
        
        <Button 
          variant="contained" 
          color="primary"
          onClick={() => navigate('/api-keys')}
        >
          API
        </Button>
      </CardContent>
    </Card>
  );

  // 
  const renderSyncContent = () => (
    <SyncSettingsContent />
  );

  // 
  const renderNotificationContent = () => (
    <Card>
      <CardContent>
        <Typography variant="h5" component="h2" gutterBottom>
          
        </Typography>
        <Divider sx={{ mb: 3 }} />
        
        <Alert severity="info" sx={{ mb: 3 }}>
          
        </Alert>
        
        {/*  */}
        <Typography variant="body1">
          
        </Typography>
      </CardContent>
    </Card>
  );

  // 
  const renderSystemInfoContent = () => (
    <Card>
      <CardContent>
        <Typography variant="h5" component="h2" gutterBottom>
          
        </Typography>
        <Divider sx={{ mb: 3 }} />
        
        <Grid container spacing={3}>
          <Grid item xs={12} md={6}>
            <Typography variant="subtitle2" color="text.secondary">
              
            </Typography>
            <Typography variant="body1">
              Coincheck 
            </Typography>
          </Grid>
          
          <Grid item xs={12} md={6}>
            <Typography variant="subtitle2" color="text.secondary">
              
            </Typography>
            <Typography variant="body1">
              1.0.0
            </Typography>
          </Grid>
          
          <Grid item xs={12} md={6}>
            <Typography variant="subtitle2" color="text.secondary">
              
            </Typography>
            <Typography variant="body1">
              2023-05-01
            </Typography>
          </Grid>
          
          <Grid item xs={12} md={6}>
            <Typography variant="subtitle2" color="text.secondary">
              
            </Typography>
            <Typography variant="body1">
              
            </Typography>
          </Grid>
        </Grid>
      </CardContent>
    </Card>
  );

  // 
  const renderContent = () => {
    switch (selectedMenu) {
      case 'profile':
        return renderProfileContent();
      case 'security':
        return renderSecurityContent();
      case 'apiKey':
        return renderApiKeyContent();
      case 'sync':
        return renderSyncContent();
      case 'notification':
        return renderNotificationContent();
      case 'systemInfo':
        return renderSystemInfoContent();
      default:
        return renderProfileContent();
    }
  };

  return (
    <Box sx={{ pb: 4 }}>
      {/*  */}
      <Typography variant="h4" component="h1" sx={{ mb: 3 }}>
        
      </Typography>
      
      <Grid container spacing={3}>
        {/*  */}
        <Grid item xs={12} md={3}>
          <Paper sx={{ width: '100%' }}>
            <MenuList>
              <MenuItem 
                selected={selectedMenu === 'profile'} 
                onClick={() => handleMenuSelect('profile')}
              >
                <ListItemIcon>
                  <PersonIcon fontSize="small" />
                </ListItemIcon>
                <ListItemText></ListItemText>
              </MenuItem>
              <MenuItem 
                selected={selectedMenu === 'security'} 
                onClick={() => handleMenuSelect('security')}
              >
                <ListItemIcon>
                  <SecurityIcon fontSize="small" />
                </ListItemIcon>
                <ListItemText></ListItemText>
              </MenuItem>
              <MenuItem 
                selected={selectedMenu === 'apiKey'} 
                onClick={() => handleMenuSelect('apiKey')}
              >
                <ListItemIcon>
                  <VpnKeyIcon fontSize="small" />
                </ListItemIcon>
                <ListItemText>API</ListItemText>
              </MenuItem>
              <MenuItem 
                selected={selectedMenu === 'sync'} 
                onClick={() => handleMenuSelect('sync')}
              >
                <ListItemIcon>
                  <SyncIcon fontSize="small" />
                </ListItemIcon>
                <ListItemText></ListItemText>
              </MenuItem>
              <MenuItem 
                selected={selectedMenu === 'notification'} 
                onClick={() => handleMenuSelect('notification')}
              >
                <ListItemIcon>
                  <NotificationsIcon fontSize="small" />
                </ListItemIcon>
                <ListItemText></ListItemText>
              </MenuItem>
              <MenuItem 
                selected={selectedMenu === 'systemInfo'} 
                onClick={() => handleMenuSelect('systemInfo')}
              >
                <ListItemIcon>
                  <InfoIcon fontSize="small" />
                </ListItemIcon>
                <ListItemText></ListItemText>
              </MenuItem>
            </MenuList>
          </Paper>
        </Grid>
        
        {/*  */}
        <Grid item xs={12} md={9}>
          {renderContent()}
        </Grid>
      </Grid>
    </Box>
  );
};

export default SettingsPage;