import React, { useState, useEffect } from 'react';
import { useParams, useNavigate } from 'react-router-dom';
import {
  Box,
  Typography,
  TextField,
  Button,
  Card,
  CardContent,
  CircularProgress,
  Grid,
  FormControlLabel,
  Switch,
  InputAdornment,
  IconButton,
  Alert,
  Breadcrumbs,
  Link,
} from '@mui/material';
import {
  Save as SaveIcon,
  Visibility as VisibilityIcon,
  VisibilityOff as VisibilityOffIcon,
  ArrowBack as ArrowBackIcon,
} from '@mui/icons-material';
import { toast } from 'react-toastify';

import apiKeyService from '../services/apiKeyService';

const ApiKeyDetailPage = () => {
  const { id } = useParams();
  const navigate = useNavigate();
  const isEditMode = Boolean(id);
  
  // 
  const [apiKey, setApiKey] = useState({
    name: '',
    accessKey: '',
    secretKey: '',
    btcWalletAddress: '',
    isActive: true,
  });
  const [loading, setLoading] = useState(false);
  const [saving, setSaving] = useState(false);
  const [error, setError] = useState(null);
  
  // 
  const [showSecretKey, setShowSecretKey] = useState(false);
  
  // API
  useEffect(() => {
    const loadApiKey = async () => {
      if (!isEditMode) return;
      
      try {
        setLoading(true);
        setError(null);
        
        const data = await apiKeyService.getApiKeyById(id, true);
        setApiKey(data);
      } catch (error) {
        console.error('API:', error);
        setError(error.message);
      } finally {
        setLoading(false);
      }
    };
    
    loadApiKey();
  }, [id, isEditMode]);
  
  // 
  const handleChange = (e) => {
    const { name, value, checked } = e.target;
    
    // Switch
    if (name === 'isActive') {
      setApiKey((prev) => ({ ...prev, [name]: checked }));
    } else {
      setApiKey((prev) => ({ ...prev, [name]: value }));
    }
  };
  
  // 
  const handleSubmit = async (e) => {
    e.preventDefault();
    
    try {
      setSaving(true);
      setError(null);
      
      if (isEditMode) {
        // API
        await apiKeyService.updateApiKey(id, apiKey);
        toast.success('API');
      } else {
        // API
        await apiKeyService.createApiKey(apiKey);
        toast.success('API');
      }
      
      // API
      navigate('/api-keys');
    } catch (error) {
      console.error('API:', error);
      setError(error.message);
    } finally {
      setSaving(false);
    }
  };
  
  // 
  if (loading && isEditMode) {
    return (
      <Box sx={{ display: 'flex', justifyContent: 'center', alignItems: 'center', height: '70vh' }}>
        <CircularProgress />
      </Box>
    );
  }

  return (
    <Box sx={{ pb: 4 }}>
      {/*  */}
      <Breadcrumbs sx={{ mb: 3 }}>
        <Link
          underline="hover"
          color="inherit"
          sx={{ cursor: 'pointer' }}
          onClick={() => navigate('/')}
        >
          
        </Link>
        <Link
          underline="hover"
          color="inherit"
          sx={{ cursor: 'pointer' }}
          onClick={() => navigate('/api-keys')}
        >
          API
        </Link>
        <Typography color="text.primary">
          {isEditMode ? 'API' : 'API'}
        </Typography>
      </Breadcrumbs>
      
      {/*  */}
      <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', mb: 3 }}>
        <Typography variant="h4" component="h1">
          {isEditMode ? 'API' : 'API'}
        </Typography>
        <Button
          variant="outlined"
          startIcon={<ArrowBackIcon />}
          onClick={() => navigate('/api-keys')}
        >
          
        </Button>
      </Box>
      
      {/*  */}
      {error && (
        <Alert severity="error" sx={{ mb: 3 }}>
          {error}
        </Alert>
      )}
      
      {/* API */}
      <Card>
        <CardContent>
          <Box component="form" onSubmit={handleSubmit} noValidate>
            <Grid container spacing={3}>
              <Grid item xs={12}>
                <TextField
                  required
                  fullWidth
                  id="name"
                  name="name"
                  label="API"
                  value={apiKey.name}
                  onChange={handleChange}
                  disabled={saving}
                  helperText="API"
                />
              </Grid>
              
              <Grid item xs={12}>
                <TextField
                  required
                  fullWidth
                  id="accessKey"
                  name="accessKey"
                  label=""
                  value={apiKey.accessKey}
                  onChange={handleChange}
                  disabled={saving}
                  helperText="Coincheck"
                />
              </Grid>
              
              <Grid item xs={12}>
                <TextField
                  required
                  fullWidth
                  id="secretKey"
                  name="secretKey"
                  label=""
                  type={showSecretKey ? 'text' : 'password'}
                  value={apiKey.secretKey}
                  onChange={handleChange}
                  disabled={saving}
                  helperText="Coincheck"
                  InputProps={{
                    endAdornment: (
                      <InputAdornment position="end">
                        <IconButton
                          aria-label=""
                          onClick={() => setShowSecretKey(!showSecretKey)}
                          edge="end"
                        >
                          {showSecretKey ? <VisibilityOffIcon /> : <VisibilityIcon />}
                        </IconButton>
                      </InputAdornment>
                    ),
                  }}
                />
              </Grid>
              
              <Grid item xs={12}>
                <TextField
                  required
                  fullWidth
                  id="btcWalletAddress"
                  name="btcWalletAddress"
                  label="BTC"
                  value={apiKey.btcWalletAddress}
                  onChange={handleChange}
                  disabled={saving}
                  helperText="BTC"
                />
              </Grid>
              
              {isEditMode && (
                <Grid item xs={12}>
                  <FormControlLabel
                    control={
                      <Switch
                        checked={apiKey.isActive}
                        onChange={handleChange}
                        name="isActive"
                        color="primary"
                      />
                    }
                    label="API"
                  />
                  <Typography variant="body2" color="text.secondary">
                    API
                  </Typography>
                </Grid>
              )}
              
              <Grid item xs={12}>
                <Box sx={{ display: 'flex', justifyContent: 'space-between' }}>
                  <Button
                    type="button"
                    variant="outlined"
                    onClick={() => navigate('/api-keys')}
                    disabled={saving}
                  >
                    
                  </Button>
                  <Button
                    type="submit"
                    variant="contained"
                    startIcon={saving ? <CircularProgress size={20} /> : <SaveIcon />}
                    disabled={saving}
                  >
                    {isEditMode ? '' : ''}
                  </Button>
                </Box>
              </Grid>
            </Grid>
          </Box>
        </CardContent>
      </Card>
    </Box>
  );
};

export default ApiKeyDetailPage;