import React, { useState, useEffect } from 'react';
import { useParams, useNavigate } from 'react-router-dom';
import {
  Box,
  Typography,
  Card,
  CardContent,
  CircularProgress,
  Grid,
  Chip,
  Divider,
  Button,
  Link,
  Alert,
  Breadcrumbs,
  IconButton,
} from '@mui/material';
import {
  ArrowBack as ArrowBackIcon,
  Receipt as ReceiptIcon,
} from '@mui/icons-material';

import transactionService from '../services/transactionService';
import { formatDateTime } from '../utils/dateUtils';

// 
const getTypeColor = (type) => {
  switch (type) {
    case 'deposit':
      return 'primary';
    case 'purchase':
      return 'info';
    case 'transfer':
      return 'success';
    case 'error':
      return 'error';
    default:
      return 'default';
  }
};

// 
const getStatusColor = (status) => {
  switch (status) {
    case 'completed':
      return 'success';
    case 'pending':
      return 'warning';
    case 'failed':
      return 'error';
    default:
      return 'default';
  }
};

// 
const getTypeName = (type) => {
  switch (type) {
    case 'deposit':
      return '';
    case 'purchase':
      return '';
    case 'transfer':
      return '';
    case 'error':
      return '';
    default:
      return type;
  }
};

const TransactionDetailPage = () => {
  const { id } = useParams();
  const navigate = useNavigate();
  
  // 
  const [transaction, setTransaction] = useState(null);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);
  
  // 
  useEffect(() => {
    const loadTransaction = async () => {
      try {
        setLoading(true);
        setError(null);
        
        const data = await transactionService.getTransactionById(id);
        setTransaction(data);
      } catch (error) {
        console.error(':', error);
        setError(error.message);
      } finally {
        setLoading(false);
      }
    };
    
    loadTransaction();
  }, [id]);
  
  // 
  if (loading) {
    return (
      <Box sx={{ display: 'flex', justifyContent: 'center', alignItems: 'center', height: '70vh' }}>
        <CircularProgress />
      </Box>
    );
  }
  
  // 
  if (error) {
    return (
      <Box sx={{ py: 4 }}>
        <Alert severity="error" sx={{ mb: 3 }}>
          {error}
        </Alert>
        <Button
          variant="outlined"
          startIcon={<ArrowBackIcon />}
          onClick={() => navigate('/transactions')}
        >
          
        </Button>
      </Box>
    );
  }
  
  // 
  if (!transaction) {
    return (
      <Box sx={{ py: 4 }}>
        <Alert severity="warning" sx={{ mb: 3 }}>
          
        </Alert>
        <Button
          variant="outlined"
          startIcon={<ArrowBackIcon />}
          onClick={() => navigate('/transactions')}
        >
          
        </Button>
      </Box>
    );
  }

  return (
    <Box sx={{ pb: 4 }}>
      {/*  */}
      <Breadcrumbs sx={{ mb: 3 }}>
        <Link
          underline="hover"
          color="inherit"
          sx={{ cursor: 'pointer' }}
          onClick={() => navigate('/')}
        >
          
        </Link>
        <Link
          underline="hover"
          color="inherit"
          sx={{ cursor: 'pointer' }}
          onClick={() => navigate('/transactions')}
        >
          
        </Link>
        <Typography color="text.primary">
           #{transaction.id}
        </Typography>
      </Breadcrumbs>
      
      {/*  */}
      <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', mb: 3 }}>
        <Box sx={{ display: 'flex', alignItems: 'center' }}>
          <ReceiptIcon sx={{ mr: 1, fontSize: 32 }} />
          <Typography variant="h4" component="h1">
             #{transaction.id}
          </Typography>
        </Box>
        <Button
          variant="outlined"
          startIcon={<ArrowBackIcon />}
          onClick={() => navigate('/transactions')}
        >
          
        </Button>
      </Box>
      
      {/*  */}
      <Card sx={{ mb: 3 }}>
        <CardContent>
          <Grid container spacing={3}>
            <Grid item xs={12} md={6}>
              <Typography variant="subtitle2" color="text.secondary">
                
              </Typography>
              <Box sx={{ mt: 1 }}>
                <Chip
                  label={getTypeName(transaction.type)}
                  color={getTypeColor(transaction.type)}
                />
              </Box>
            </Grid>
            
            <Grid item xs={12} md={6}>
              <Typography variant="subtitle2" color="text.secondary">
                
              </Typography>
              <Box sx={{ mt: 1 }}>
                <Chip
                  label={transaction.status}
                  color={getStatusColor(transaction.status)}
                />
              </Box>
            </Grid>
            
            <Grid item xs={12}>
              <Divider />
            </Grid>
            
            <Grid item xs={12} md={6}>
              <Typography variant="subtitle2" color="text.secondary">
                
              </Typography>
              <Typography variant="body1">
                {formatDateTime(transaction.createdAt)}
              </Typography>
            </Grid>
            
            <Grid item xs={12} md={6}>
              <Typography variant="subtitle2" color="text.secondary">
                API
              </Typography>
              <Typography variant="body1">
                {transaction.apiKey ? (
                  <Link
                    component="button"
                    variant="body1"
                    onClick={() => navigate(`/api-keys/${transaction.apiKey.id}`)}
                  >
                    {transaction.apiKey.name}
                  </Link>
                ) : '-'}
              </Typography>
            </Grid>
            
            {/*  */}
            {transaction.type === 'deposit' && (
              <>
                <Grid item xs={12}>
                  <Divider textAlign="left"></Divider>
                </Grid>
                
                <Grid item xs={12} md={6}>
                  <Typography variant="subtitle2" color="text.secondary">
                    ID
                  </Typography>
                  <Typography variant="body1">
                    {transaction.depositId || '-'}
                  </Typography>
                </Grid>
                
                <Grid item xs={12} md={6}>
                  <Typography variant="subtitle2" color="text.secondary">
                    
                  </Typography>
                  <Typography variant="body1">
                    {transaction.depositAmount ? `${transaction.depositAmount} BTC` : '-'}
                  </Typography>
                </Grid>
              </>
            )}
            
            {/*  */}
            {transaction.type === 'purchase' && (
              <>
                <Grid item xs={12}>
                  <Divider textAlign="left"></Divider>
                </Grid>
                
                <Grid item xs={12} md={6}>
                  <Typography variant="subtitle2" color="text.secondary">
                    ID
                  </Typography>
                  <Typography variant="body1">
                    {transaction.purchaseId || '-'}
                  </Typography>
                </Grid>
                
                <Grid item xs={12} md={6}>
                  <Typography variant="subtitle2" color="text.secondary">
                    
                  </Typography>
                  <Typography variant="body1">
                    {transaction.purchaseAmount ? `${transaction.purchaseAmount} BTC` : '-'}
                  </Typography>
                </Grid>
                
                <Grid item xs={12} md={6}>
                  <Typography variant="subtitle2" color="text.secondary">
                    
                  </Typography>
                  <Typography variant="body1">
                    {transaction.purchaseRate ? `${transaction.purchaseRate} JPY/BTC` : '-'}
                  </Typography>
                </Grid>
              </>
            )}
            
            {/*  */}
            {transaction.type === 'transfer' && (
              <>
                <Grid item xs={12}>
                  <Divider textAlign="left"></Divider>
                </Grid>
                
                <Grid item xs={12} md={6}>
                  <Typography variant="subtitle2" color="text.secondary">
                    ID
                  </Typography>
                  <Typography variant="body1">
                    {transaction.transferId || '-'}
                  </Typography>
                </Grid>
                
                <Grid item xs={12} md={6}>
                  <Typography variant="subtitle2" color="text.secondary">
                    
                  </Typography>
                  <Typography variant="body1">
                    {transaction.transferAmount ? `${transaction.transferAmount} BTC` : '-'}
                  </Typography>
                </Grid>
                
                <Grid item xs={12} md={6}>
                  <Typography variant="subtitle2" color="text.secondary">
                    
                  </Typography>
                  <Typography variant="body1" sx={{ wordBreak: 'break-all' }}>
                    {transaction.walletAddress || '-'}
                  </Typography>
                </Grid>
              </>
            )}
            
            {/*  */}
            {transaction.type === 'error' && (
              <>
                <Grid item xs={12}>
                  <Divider textAlign="left"></Divider>
                </Grid>
                
                <Grid item xs={12}>
                  <Typography variant="subtitle2" color="text.secondary">
                    
                  </Typography>
                  <Alert severity="error" sx={{ mt: 1 }}>
                    {transaction.errorMessage || ''}
                  </Alert>
                </Grid>
              </>
            )}
          </Grid>
        </CardContent>
      </Card>
      
      {/* rawData */}
      {transaction.rawData && (
        <Card>
          <CardContent>
            <Typography variant="h6" component="h2" gutterBottom>
              Console log
            </Typography>
            <Box
              component="pre"
              sx={{
                p: 2,
                bgcolor: 'background.paper',
                borderRadius: 1,
                overflow: 'auto',
                maxHeight: 300,
              }}
            >
              {JSON.stringify(transaction.rawData, null, 2)}
            </Box>
          </CardContent>
        </Card>
      )}
    </Box>
  );
};

export default TransactionDetailPage;